.dropdown {
	 margin-left: 5%;
	 display: none;
	 width: 90%;
	 max-height: 100%;
	 background-color: #fff;
	 color: black;
	 overflow: hidden;
	 overflow-y: scroll;
	 position: absolute;
	 z-index: 5;
}
 .dropdown.active {
	 display: block;
}
 .dropdown div {
	 padding-top: 3px;
	 padding-bottom: 3px;
	 border: 1px solid #eaeaea;
}